import { Routes } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { CallbackComponent } from './callback/callback.component';
import { ProfileComponent } from './profile/profile.component';
import { PingComponent } from './ping/ping.component';
import { Asociado1Component } from './asociado1/asociado1.component';
import { Prestador1Component } from './prestador1/prestador1.component';
export const ROUTES: Routes = [
  { path: '', component: HomeComponent },
  { path: 'callback', component: CallbackComponent },
  { path: 'profile', component: ProfileComponent },
  { path: 'ping', component: PingComponent },
  { path: 'asociado1', component: Asociado1Component },
  { path: 'prestador1', component: Prestador1Component },
  { path: '**', redirectTo: '' }
];



