// src/app/ping/ping.component.ts
import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';

interface IApiResponse {
  message: string;
}

@Component({
  selector: 'app-ping',
  templateUrl: './ping.component.html',
  styleUrls: ['./ping.component.css']
})
export class PingComponent {

  API_URL: string = 'http://localhost';
  message: string;

  constructor(public http: HttpClient) {}

  public securedPing(): void {
    this.message = '';
	var state = `${localStorage.getItem('state')}`;
    this.http
      .get<IApiResponse>(`${this.API_URL}/medife/ping_seguro?code=xx&state=`+state, {
        headers: new HttpHeaders().set('Authorization', `Bearer ${localStorage.getItem('access_token')}`)
      })
      .subscribe(
        data => this.message = data.message,
        error => this.message = error
      );
  }

  public securedScopedPing(): void {
    this.message = '';
    this.http
      .get<IApiResponse>(`${this.API_URL}/medife-private-scoped`, {
        headers: new HttpHeaders().set('Authorization', `Bearer ${localStorage.getItem('access_token')}`)
    })
      .subscribe(
        data => this.message = data.message,
        error => this.message = error
      );
    }
}


